import React from 'react';
import { AbsoluteFill, useCurrentFrame, interpolate, spring, useVideoConfig, Video } from 'remotion';
import { PhoneVideo } from './components/PhoneVideo';

export const ViralReel = ({ 
  primaryColor, 
  videoSource, 
  texts, 
  lang 
}: any) => {
  const frame = useCurrentFrame();
  const { fps, width, height } = useVideoConfig();

  // 1. Animasyon: Arka Plan Hareketi (YavaÅŸÃ§a zoom yapar)
  const bgScale = interpolate(frame, [0, 300], [1, 1.2]);
  
  // 2. Animasyon: Telefonun aÅŸaÄŸÄ±dan yukarÄ± geliÅŸi (Spring)
  const phoneEntrance = spring({
    frame,
    fps,
    config: { damping: 12 },
    from: 1000, // EkranÄ±n altÄ±ndan
    to: 0
  });

  // 3. Animasyon: YazÄ±larÄ±n parlayarak gelmesi
  const textOpacity = interpolate(frame, [20, 40], [0, 1]);

  return (
    <AbsoluteFill style={{ backgroundColor: '#f1f5f9' }}>
      
      {/* ARKA PLAN (BulanÄ±k Video veya Gradient) */}
      <AbsoluteFill style={{ overflow: 'hidden' }}>
         <div className="absolute inset-0 bg-gradient-to-br from-purple-500 to-orange-400 opacity-80" />
         {/* Arka planda silik, bulanÄ±k ÅŸekilde aynÄ± video dÃ¶nebilir (Trend stil) */}
         <Video 
            src={videoSource} 
            style={{ 
                ...AbsoluteFill, 
                filter: 'blur(40px)', 
                opacity: 0.5,
                transform: `scale(${bgScale})` 
            }} 
         />
      </AbsoluteFill>

      {/* ANA Ä°Ã‡ERÄ°K */}
      <AbsoluteFill className="flex flex-col items-center justify-center">
        
        {/* Ãœst BaÅŸlÄ±k (Hook) */}
        <div 
            style={{ opacity: textOpacity, transform: `translateY(${phoneEntrance * 0.1}px)` }}
            className="z-20 mb-10 text-center"
        >
            <h1 className="text-6xl font-black text-white drop-shadow-xl uppercase tracking-tighter bg-black/20 px-6 py-2 rounded-xl backdrop-blur-sm">
                {texts[lang].hook}
            </h1>
        </div>

        {/* Telefon Sahnesi */}
        <div style={{ transform: `translateY(${phoneEntrance}px)` }} className="z-10">
            <PhoneVideo videoSrc={videoSource} />
        </div>

        {/* Alt Ã‡aÄŸrÄ± (CTA) */}
        <div 
            style={{ opacity: textOpacity }}
            className="absolute bottom-20 z-20 bg-white text-black px-12 py-4 rounded-full font-bold text-3xl shadow-2xl animate-pulse"
        >
            {texts[lang].cta}
        </div>

      </AbsoluteFill>
      
      {/* Progress Bar (Reels iÃ§in olmazsa olmaz) */}
      <div 
        style={{ 
            width: `${(frame / (30 * 15)) * 100}%`, // 15 saniye bazlÄ±
            height: 10,
            background: primaryColor,
            position: 'absolute',
            bottom: 0,
            left: 0,
            zIndex: 50
        }} 
      />

    </AbsoluteFill>
  );
};