import React from 'react';
import { Video, Img, absoluteFill } from 'remotion';

interface Props {
  videoSrc: string;
  frameStyle?: React.CSSProperties;
}

export const PhoneVideo: React.FC<Props> = ({ videoSrc, frameStyle }) => {
  return (
    <div 
      style={{
        ...frameStyle,
        width: 350,
        height: 700,
        borderRadius: 40,
        border: '12px solid #1e293b', // Koyu Ã§erÃ§eve
        overflow: 'hidden',
        position: 'relative',
        boxShadow: '0 25px 50px -12px rgba(0, 0, 0, 0.5)',
        background: '#000'
      }}
    >
      {/* Ekran KaydÄ± Videosu */}
      <Video 
        src={videoSrc}
        style={{
          ...absoluteFill,
          objectFit: 'cover',
        }}
        // Loop veya ses ayarlarÄ± buraya
        // startFrom={0} 
      />
      
      {/* Parlama Efekti (Overlay) */}
      <div className="absolute inset-0 bg-gradient-to-tr from-white/10 to-transparent pointer-events-none z-10" />
    </div>
  );
};